import React, { Component } from 'react';
import right from '../sym/right.png';
import { Link } from 'react-router-dom';

class BookCategory extends Component {
    render() {
        return (
            <div className="main">
                <div className="container py-2">
                    <h1 className="text-center py-5 text-light">Gutenberg Project</h1>

                    <div className="container my-4  category-container text-light">
                        <Link to='/Fiction' className="pl-5 text-light subject py-3 mb-4 d-flex" >
                            <h3 className=" my-auto" >FICTION</h3> <div className="ml-auto mr-5 my-auto"><img src={right} alt="->" /></div>
                        </Link>

                        <div className="pl-5 subject py-3 mb-4 d-flex">
                            <h3 className=" my-auto">DRAMA</h3> <div className="ml-auto mr-5 my-auto"><img src={right} alt="->" /></div>
                        </div>

                        <div className="pl-5 subject py-3 mb-4 d-flex">
                            <h3 className=" my-auto">HUMOR</h3> <div className="ml-auto mr-5 my-auto"><img src={right} alt="->" /></div>
                        </div>

                        <div className="pl-5 subject py-3 mb-4 d-flex">
                            <h3 className=" my-auto">POLITICS</h3> <div className="ml-auto mr-5 my-auto"><img src={right} alt="->" /></div>
                        </div>

                        <div className="pl-5 subject py-3 mb-4 d-flex">
                            <h3 className=" my-auto">PHILOSOPHY</h3> <div className="ml-auto mr-5 my-auto"><img src={right} alt="->" /></div>
                        </div>

                        <div className="pl-5 subject py-3 mb-4 d-flex">
                            <h3 className=" my-auto">HISTORY</h3> <div className="ml-auto mr-5 my-auto"><img src={right} alt="->" /></div>
                        </div>

                        <div className="pl-5 subject py-3 mb-4 d-flex">
                            <h3 className=" my-auto">ADVENTURE</h3> <div className="ml-auto mr-5 my-auto"><img src={right} alt="->" /></div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default BookCategory;